<template>
    <div class="p-fluid">
        <div
            class="p-field p-mt-2"
            :class="{
                'p-invalid': submitted && v$.username.$invalid
            }"
        >
            <label>Username</label>
            <p-input-text
                v-model="form.username"
                :class="{
                    'p-invalid': submitted && v$.username.$invalid
                }"
            />
        </div>
        <div class="p-field p-mt-2">
            <label>Name</label>
            <p-input-text
                v-model="form.name"
                :class="{
                    'p-invalid': submitted && v$.name.$invalid
                }"
            />
        </div>
        <div
            v-if="(v$.name.$invalid && submitted) || v$.name.$pending.$response"
            class="p-field p-error"
        >
            {{ v$.name.required.$message.replace("Value", "Name") }}
        </div>

        <div class="p-field p-mt-2">
            <label>Email</label>
            <p-input-text
                v-model="form.email"
                :class="{
                    'p-invalid': submitted && v$.email.$invalid
                }"
            />
        </div>
        <div
            v-if="
                (v$.email.$invalid && submitted) || v$.email.$pending.$response
            "
            class="p-field p-error"
        >
            Email required or not valid email
        </div>
        <div
            v-if="
                (v$.username.$invalid && submitted) ||
                    v$.username.$pending.$response
            "
            class="p-field p-error"
        >
            {{ v$.username.required.$message.replace("Value", "Username") }}
        </div>

        <div class="p-field p-mt-2">
            <label>Phone</label>
            <p-input-text v-model="form.phone" />
        </div>

        <div class="p-field p-mt-2" v-if="isCreateForm">
            <label>Password</label>
            <p-password
                v-model="form.password"
                :feedback="false"
                :class="{
                    'p-invalid': submitted && v$.password.$invalid
                }"
            />
        </div>
        <div
            v-if="
                (v$.password.$invalid && submitted) ||
                    v$.password.$pending.$response
            "
            class="p-field p-error"
        >
            {{ v$.password.required.$message.replace("Value", "Password") }}
        </div>

        <div class="p-field p-mt-2" v-if="isCreateForm">
            <label>Password Confirm</label>
            <p-password
                v-model="form.passwordConfirm"
                :feedback="false"
                :class="{
                    'p-invalid':
                        submitted &&
                        (v$.passwordConfirm.$invalid || passwordDidnotMatch)
                }"
            />
        </div>
        <div
            v-if="
                (v$.passwordConfirm.$invalid && submitted) ||
                    v$.passwordConfirm.$pending.$response
            "
            class="p-field p-error"
        >
            {{
                v$.passwordConfirm.required.$message.replace(
                    "Value",
                    "Password"
                )
            }}
        </div>

        <div v-if="passwordDidnotMatch && submitted" class="p-field p-error">
            Password didn't match
        </div>

        <div class="p-field p-mt-2">
            <label class="p-mr-2">Multi Login</label>
            <p-dropdown
                v-model="form.role"
                optionLabel="label"
                optionValue="value"
                :options="userRoleOptions"
            />
        </div>

        <div class="p-field p-mt-2">
            <label class="p-mr-2">Group</label>
            <p-multi-select
                v-model="form.security"
                optionLabel="groupName"
                optionValue="gId"
                :options="groupOptions"
            />
        </div>
    </div>

    <p-button
        :label="buttonLabel"
        class="p-mt-2"
        @click="onSave"
        :loading="loading"
    />
</template>

<script>
import { useUserForm } from "@/composable/users";
import { computed, onMounted, ref } from "vue-demi";
import { FORM_TYPE } from "@/config";
import { useRoute } from "vue-router";

export default {
    props: {
        type: {
            type: String,
            required: true
        }
    },
    emits: ["onSave"],
    setup(props, context) {
        const {
            store,
            form,
            v$,
            submitted,
            buttonLabel,
            userRoleOptions,
            passwordDidnotMatch,
            groupOptions
        } = useUserForm(props.type);

        const loading = ref(false);
        const onSave = () => {
            location.value = true;
            submitted.value = true;
            context.emit(
                "onSave",
                !v$.value.$invalid && form.password === form.passwordConfirm,
                { ...form }
            );
        };

        const route = useRoute();

        onMounted(() => {
            if (props.type === FORM_TYPE.EDIT) {
                let user = store.state.users.users.find(
                    (item) => item.id == route.params.id
                );

                form.name = user.name;
                form.email = user.email;
                form.username = user.username;
                form.phone = user.phone;
                form.role = user.privilage;
                form.security = user.security;
            }

            store.dispatch("users/getUserGroups");
        });

        return {
            form,
            v$,
            submitted,
            buttonLabel,
            onSave,
            userRoleOptions,
            passwordDidnotMatch,
            groupOptions,
            isCreateForm: computed(() => props.type === FORM_TYPE.CREATE),
            loading
        };
    }
};
</script>
