import { computed, reactive, ref } from "vue";
import { email, required, requiredIf, sameAs } from "@vuelidate/validators";
import useVuelidate from "@vuelidate/core";
import { FORM_TYPE, USER_ROLE } from "@/config";
import { useStore } from "vuex";
import { useRoute } from "vue-router";

export function useUserForm(type) {
    const store = useStore();
    const buttonLabel = computed(() => {
        if (type === FORM_TYPE.CREATE) {
            return "Save";
        }
        if (type === FORM_TYPE.EDIT) {
            return "Save";
        }
    });

    const form = reactive({
        name: null,
        email: null,
        username: null,
        phone: null,
        password: null,
        passwordConfirm: null,
        role: null,
        active: true,
        security: []
    });

    const rules = {
        name: { required },
        email: { required, email },
        username: { required },
        password: { required: requiredIf(() => type === FORM_TYPE.CREATE) },
        passwordConfirm: {
            required: requiredIf(() => type === FORM_TYPE.CREATE)
        }
    };

    const passwordDidnotMatch = computed(
        () => form.password !== form.passwordConfirm
    );

    const v$ = useVuelidate(rules, form);
    const submitted = ref(false);

    const userRoleOptions = [
        { label: "Yes", value: "2" },
        { label: "No", value: "1" }
    ];

    const groupOptions = computed(() => store.state.users.groups);

    return {
        store,
        form,
        v$,
        submitted,
        buttonLabel,
        userRoleOptions,
        passwordDidnotMatch,
        groupOptions
    };
}

export function usePermissionCheck() {
    const store = useStore();
    const route = useRoute();

    const permission = computed(() => {
        const idx = store.state.user.permissions.findIndex(
            (item) => `/${item.appKey}` === route.path
        );
        return store.state.user.permissions[idx];
    });

    const hasUpdatePermission = computed(
        () => permission.value.updatePermission
    );

    const hasCreatePermission = computed(
        () => permission.value.updatePermission
    );

    const hasDeletePermission = computed(
        () => permission.value.deletePermission
    );

    const hasBornCSVPermission = computed(() => {
        const idx = store.state.user.permissions.findIndex(
            (item) => item.appKey === "born-csv"
        );

        if (idx > 0) {
            return store.state.user.permissions[idx].readPermission;
        }
        return false;
    });

    return {
        hasUpdatePermission,
        hasCreatePermission,
        hasDeletePermission,
        hasBornCSVPermission
    };
}
