<template>
    <div class="p-d-flex p-jc-center">
        <p-card class="p-col-4">
            <template #content>
                <user-form :type="formType" @onSave="save" />
            </template>
        </p-card>
    </div>
</template>

<script>
import UserForm from "@/components/users/form.vue";
import { FORM_TYPE } from "@/config";
import { useRoute, useRouter } from "vue-router";
import { useStore } from "vuex";
import { useToast } from "primevue/usetoast";

export default {
    components: {
        UserForm
    },
    setup() {
        const route = useRoute();
        const router = useRouter();
        const store = useStore();
        const toast = useToast();

        const save = (isValid, form) => {
            if (!isValid) {
                return;
            }

            store
                .dispatch("users/updateUser", {
                    id: route.params.id,
                    payload: {
                        ...form,
                        privilage: form.role
                    }
                })
                .then(() => {
                    toast.add({
                        severity: "success",
                        summary: "Success",
                        detail: "Update Successfull",
                        life: 3000
                    });
                    router.push({ path: "/maintanance/users" });
                });
        };

        return {
            save,
            formType: FORM_TYPE.EDIT
        };
    }
};
</script>
